<script>
import AdminService from '@/api-services/admin.service';

    import doctorlist from '../../assets/json/admin/AdminIndex/doctorlist.json'
    import patientlist from '../../assets/json/admin/AdminIndex/patientlist.json'
    import appointmentlist from '../../assets/json/admin/AdminIndex/appointmentlist.json'
    const images = require.context('@/assets/admin_img/doctors', false, /\.png$|\.jpg$/)
    const patientImages = require.context('@/assets/admin_img/patients', false, /\.png$|\.jpg$/)
    export default {
        data() {
		return {
			doctorlist: doctorlist,
            patientlist: patientlist,
            appointmentlist: appointmentlist,
            admin_data_to_show:{},
		}
    },
    created(){
        if(window.localStorage.getItem('role') =='admin') {
            this.getDashboadData();
        }
        else{
            this.$router.push('/admin/login');
        }
    },
    methods:{
        getDashboadData(){
            let token = window.localStorage.getItem('token');
            AdminService.dashboardService(token).then((response) => {
                if(response.status==200){
                    this.admin_data_to_show = response.data.data;
                }
                else{
					alert('fail');
				}

            }).catch((error) => {
				
			});

        },
        loadImg(imgPath, value) {
            if(value == 1) {
              return  images('./' + imgPath)
            } else {
              return  patientImages('./' + imgPath)
            }
        },
    },
    mounted() {
        $(function(){
	
	/* Morris Area Chart */
	
	window.mA = Morris.Area({
	    element: 'morrisArea',
	    data: [
	        { y: '2013', a: 60},
	        { y: '2014', a: 100},
	        { y: '2015', a: 240},
	        { y: '2016', a: 120},
	        { y: '2017', a: 80},
	        { y: '2018', a: 100},
	        { y: '2019', a: 300},
	    ],
	    xkey: 'y',
	    ykeys: ['a'],
	    labels: ['Revenue'],
	    lineColors: ['#17a2b8'],
	    lineWidth: 2,
		
     	fillOpacity: 0.5,
	    gridTextSize: 10,
	    hideHover: 'auto',
	    resize: true,
		redraw: true
	});
	
	/* Morris Line Chart */
	
	window.mL = Morris.Line({
	    element: 'morrisLine',
	    data: [
	        { y: '2015', a: 100, b: 30},
	        { y: '2016', a: 20,  b: 60},
	        { y: '2017', a: 90,  b: 120},
	        { y: '2018', a: 50,  b: 80},
	        { y: '2019', a: 120,  b: 150},
	    ],
	    xkey: 'y',
	    ykeys: ['a', 'b'],
	    labels: ['Doctors', 'Patients'],
	    lineColors: ['#1b5a90','#28a745'],
	    lineWidth: 1,
	    gridTextSize: 10,
	    hideHover: 'auto',
	    resize: true,
		redraw: true
	});
	$(window).on("resize", function(){
		mA.redraw();
		mL.redraw();
	});
});
        $(document).on('click', '#toggle_btn', function() {
		if($('body').hasClass('mini-sidebar')) {
			$('body').removeClass('mini-sidebar');
			$('.subdrop + ul').slideDown();
		} else {
			$('body').addClass('mini-sidebar');
			$('.subdrop + ul').slideUp();
		}
		return false;
	});
	$(document).on('mouseover', function(e) {
		e.stopPropagation();
		if($('body').hasClass('mini-sidebar') && $('#toggle_btn').is(':visible')) {
			var targ = $(e.target).closest('.sidebar').length;
			if(targ) {
				$('body').addClass('expand-menu');
				$('.subdrop + ul').slideDown();
			} else {
				$('body').removeClass('expand-menu');
				$('.subdrop + ul').slideUp();
			}
			return false;
		}
	});
},

    }
</script>
